<template>
  <v-card max-width="550" max-height="600">
    <v-carousel
      v-model="carouselModel"
      :cycle="post.images.length > 1"
      hide-delimiter-background
      hide-delimiters
      :show-arrows="post.images.length > 1"
    >
      <v-carousel-item
        v-for="(image, index) in post.images"
        :key="'postN' + index"
        :src="image"
      ></v-carousel-item>
    </v-carousel>
    <v-card-title>{{ post.title }}</v-card-title>
    <v-card-text>
      <div>
        {{ post.description }}
      </div>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  props: {
    post: {
      type: Object,
      default: Object,
      required: true,
    },
  },
  data() {
    return {
      carouselModel: 0,
    };
  },
};
</script>

<style lang="scss" scoped></style>
